import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';

// REDUX
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';

// ACTION
import * as dateActions from '../store/modules/date';
import * as salesActions from '../store/modules/sales';
import * as spendsActions from '../store/modules/spends';

// COMPONENT
import DatePickerComponent from '../components/DatePickerComponent';

class DatePickerContainer extends PureComponent {
  static propTypes = {
    date: PropTypes.objectOf(String).isRequired,
    DateActions: PropTypes.objectOf(Function).isRequired,
    SalesActions: PropTypes.objectOf(Function).isRequired,
    SpendsActions: PropTypes.objectOf(Function).isRequired,
  };

  componentDidUpdate() {
    const { date, SalesActions, SpendsActions } = this.props;
    SalesActions.fatchsales(date);
    SpendsActions.fatchspends(date);
  }

  render() {
    const { date, DateActions } = this.props;
    return (
      <DatePickerComponent
        date={date.date}
        increasedate={DateActions.increasedate}
        decreasedate={DateActions.decreasedate}
      />
    );
  }
}

const mapStateToProps = state => ({
  date: state.date,
});

const mapDispatchToProps = dispatch => ({
  DateActions: bindActionCreators(dateActions, dispatch),
  SalesActions: bindActionCreators(salesActions, dispatch),
  SpendsActions: bindActionCreators(spendsActions, dispatch),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(DatePickerContainer);
