import { createAction, handleActions } from 'redux-actions';
import { applyPenders } from 'redux-pender';
import axios from 'axios';
import produce from 'immer';

const apiUri = '/api/sales';

const CREATE_SALES = 'sales/CREATE_SALES';
const FATCH_SALES = 'sales/FATCH_SALES';
const EDIT_SALES = 'sales/EDIT_SALES';
const DELETE_SALES = 'sales/DELETE_SALES';

const createSalesInfo = data => {
  return axios.post(`${apiUri}/create`, data);
};

const fatchSalesInfo = date => {
  return axios.post(`${apiUri}/fatch`, date);
};

const editSalesInfo = data => {
  return axios.post(`${apiUri}/edit`, data);
};

const deleteSalesInfo = data => {
  return axios.post(`${apiUri}/delete`, data);
};

export const createsales = createAction(CREATE_SALES, createSalesInfo);
export const fatchsales = createAction(FATCH_SALES, fatchSalesInfo);
export const editsales = createAction(EDIT_SALES, editSalesInfo);
export const deletesales = createAction(DELETE_SALES, deleteSalesInfo);

const initialState = {
  value: [],
  values: [],
};

const reducer = handleActions({}, initialState);

export default applyPenders(reducer, [
  {
    type: CREATE_SALES,
  },
  {
    type: FATCH_SALES,
    onSuccess: (state, action) => {
      return produce(state, draft => {
        // eslint-disable-next-line no-param-reassign
        draft.value = action.payload.data.results;
        // eslint-disable-next-line no-param-reassign
        draft.values = action.payload.data.allResults;
      });
    },
  },
  {
    type: EDIT_SALES,
  },
  {
    type: DELETE_SALES,
  },
]);
