/* eslint-disable no-alert */
/* eslint-disable no-shadow */
import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';

// REDUX
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';

// MATERIAL
import Fab from '@material-ui/core/Fab';
import Add from '@material-ui/icons/Add';

// ACTION
import * as salesActions from '../store/modules/sales';
import * as spendsActions from '../store/modules/spends';

// CONTAINER
import DialogEditContainer from './DialogEditContainer';
import DatePickerContainer from './DatePickerContainer';
import OverviewContainer from "./OverviewContainer";

// COMPONENT
import DialogDeleteComponent from '../components/DialogDeleteComponent';
import TableComponent from '../components/TableComponent';

const salesColumns = [
  { title: '판매 상품', field: 'goods' },
  { title: '업체명', field: 'company' },
  { title: '이름', field: 'name' },
  { title: '연락처', field: 'contact' },
  { title: '상품 설명', field: 'description' },
  { title: '상품 금액', field: 'price' },
  { title: '지출 금액', field: 'spend' },
  { title: '마진 금액', field: 'margin' },
  {
    title: '입금 여부',
    field: 'deposit',
    lookup: { true: '입금', false: '미입금' },
    cellStyle: data => {
      if (data === '입금') {
        return {
          color: '#66bb6a',
        };
      }
      if (data === '미입금') {
        return {
          color: '#ef5350',
        };
      }
      return null;
    },
  },
  { title: '광고주 홈페이지', field: 'homepage' },
  { title: '판매 날짜', field: 'date' },
];

const spendsColumns = [
  { title: '지출 상품', field: 'goods' },
  { title: '지출 내용', field: 'name' },
  { title: '지출 금액', field: 'price' },
  { title: '지출 날짜', field: 'date' },
];

class TableContainer extends PureComponent {
  static propTypes = {
    match: PropTypes.objectOf(String).isRequired,
    date: PropTypes.objectOf(String).isRequired,
    sales: PropTypes.arrayOf(String).isRequired,
    SalesActions: PropTypes.objectOf(Function).isRequired,
    spends: PropTypes.arrayOf(String).isRequired,
    SpendsActions: PropTypes.objectOf(Function).isRequired,
  };

  constructor(props) {
    super(props);

    this.state = {
      openEdit: false,
      openDelete: false,
      editData: {},
      deleteData: {},
    };
  }

  componentDidMount() {
    const { date, SalesActions, SpendsActions } = this.props;
    SalesActions.fatchsales(date);
    SpendsActions.fatchspends(date);
  }

  closeEdit = () => {
    this.setState({ openEdit: false });
  };

  closeDelete = () => {
    this.setState({ openDelete: false });
  };

  render() {
    const { match, sales, spends } = this.props;
    const { openEdit, openDelete, editData, deleteData } = this.state;
    const { closeEdit, closeDelete } = this;
    return (
      <>
        <DialogEditContainer
          open={openEdit}
          onClose={closeEdit}
          data={editData}
          type={match.params.type}
        />
        <DialogDeleteComponent
          open={openDelete}
          onClose={closeDelete}
          data={deleteData}
          type={match.params.type}
        />
        <Fab
          size="small"
          color="primary"
          aria-label="Add"
          onClick={() =>
            this.setState({
              openEdit: true,
              editData: {},
            })
          }
        >
          <Add />
        </Fab>
        <DatePickerContainer />
        <OverviewContainer />
        <TableComponent
          columns={match.params.type === 'sales' ? salesColumns : spendsColumns}
          data={match.params.type === 'sales' ? sales : spends}
          type={match.params.type}
          actions={[
            {
              icon: 'edit',
              tooltip: '편집',
              onClick: (event, rowData) => {
                this.setState({
                  openEdit: true,
                  editData: rowData,
                });
              },
            },
            {
              icon: 'delete',
              tooltip: '삭제',
              onClick: (event, rowData) => {
                this.setState({
                  openDelete: true,
                  deleteData: rowData,
                });
              },
            },
          ]}
        />
      </>
    );
  }
}

const mapStateToProps = state => ({
  date: state.date,
  sales: state.sales.value,
  spends: state.spends.value,
});

const mapDispatchToProps = dispatch => ({
  SalesActions: bindActionCreators(salesActions, dispatch),
  SpendsActions: bindActionCreators(spendsActions, dispatch),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(TableContainer);
