/* eslint-disable guard-for-in */
import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';

// REDUX
import { connect } from 'react-redux';

// COMPONENT
import OverviewComponent from '../components/OverviewComponent';

class OverviewContainer extends PureComponent {
  static propTypes = {
    sales: PropTypes.arrayOf(String).isRequired,
    allSales: PropTypes.arrayOf(String).isRequired,
  };

  sum = arr => {
    const results = arr.map(sale => {
      if (sale.deposit) {
        return sale.margin;
      }
      return 0;
    });
    let sum = 0;
    for (const i in results) {
      sum += results[i];
    }
    return sum;
  };

  render() {
    const { sales, allSales } = this.props;
    const { sum } = this;
    return (
      <OverviewComponent
        month={sum(sales)}
        all={sum(allSales)}
        count={sales.length}
      />
    );
  }
}

const mapStateToProps = state => ({
  sales: state.sales.value,
  allSales: state.sales.values,
});

export default connect(
  mapStateToProps,
  null,
)(OverviewContainer);
