import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';

// MATERIAL
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';

const styles = ({ spacing }) => ({
  card: {
    margin: spacing.unit * 1,
    marginTop: spacing.unit * 3,
  },
  text: {
    marginTop: spacing.unit * 3,
  },
});

class OverviewComponent extends PureComponent {
  static propTypes = {
    month: PropTypes.number.isRequired,
    all: PropTypes.number.isRequired,
    count: PropTypes.number.isRequired,
    classes: PropTypes.objectOf(String).isRequired,
  };

  render() {
    const { month, all, count, classes } = this.props;
    return (
      <>
        <Grid container spacing={16}>
          <Grid xs={12} sm={4}>
            <Card className={classes.card}>
              <CardContent>
                <Typography color="textSecondary" gutterBottom>
                  담당자 월 매출
                </Typography>
                <Typography
                  className={classes.text}
                  variant="h3"
                  component="h2"
                >
                  총 {month} 원
                </Typography>
              </CardContent>
            </Card>
          </Grid>
          <Grid xs={12} sm={4}>
            <Card className={classes.card}>
              <CardContent>
                <Typography color="textSecondary" gutterBottom>
                  담당자 누적 매출
                </Typography>
                <Typography
                  className={classes.text}
                  variant="h3"
                  component="h2"
                >
                  총 {all} 원
                </Typography>
              </CardContent>
            </Card>
          </Grid>
          <Grid xs={12} sm={4}>
            <Card className={classes.card}>
              <CardContent>
                <Typography color="textSecondary" gutterBottom>
                  월 판매 건수
                </Typography>
                <Typography
                  className={classes.text}
                  variant="h3"
                  component="h2"
                >
                  총 {count} 건
                </Typography>
              </CardContent>
            </Card>
          </Grid>
        </Grid>
      </>
    );
  }
}

export default withStyles(styles)(OverviewComponent);
