import { createAction, handleActions } from 'redux-actions';
import { applyPenders } from 'redux-pender';
import axios from 'axios';
import produce from 'immer';

const apiUri = '/api/spends';

const CREATE_SPENDS = 'spends/CREATE_SPENDS';
const FATCH_SPENDS = 'spends/FATCH_SPENDS';
const EDIT_SPENDS = 'spends/EDIT_SPENDS';
const DELETE_SPENDS = 'spends/DELETE_SPENDS';

const createSpendsInfo = data => {
  return axios.post(`${apiUri}/create`, data);
};

const fatchSpendsInfo = date => {
  return axios.post(`${apiUri}/fatch`, date);
};

const editSpendsInfo = data => {
  return axios.post(`${apiUri}/edit`, data);
};

const deleteSpendsInfo = data => {
  // eslint-disable-next-line no-underscore-dangle
  return axios.post(`${apiUri}/delete`, data);
};

export const createspends = createAction(CREATE_SPENDS, createSpendsInfo);
export const fatchspends = createAction(FATCH_SPENDS, fatchSpendsInfo);
export const editspends = createAction(EDIT_SPENDS, editSpendsInfo);
export const deletespends = createAction(DELETE_SPENDS, deleteSpendsInfo);

const initialState = {
  value: [],
};

const reducer = handleActions({}, initialState);

export default applyPenders(reducer, [
  {
    type: CREATE_SPENDS,
  },
  {
    type: FATCH_SPENDS,
    onSuccess: (state, action) => {
      return produce(state, draft => {
        // eslint-disable-next-line no-param-reassign
        draft.value = action.payload.data;
      });
    },
  },
  {
    type: EDIT_SPENDS,
  },
  {
    type: DELETE_SPENDS,
  },
]);
