import { createAction, handleActions } from 'redux-actions';
import { pender } from 'redux-pender';
import axios from 'axios';
import produce from 'immer';

const apiUri = '/api/admin';

const CREATE_USERS = 'admin/CREATE_USERS';
const FATCH_USERS = 'admin/FATCH_USERS';
const EDIT_USERS = 'admin/EDIT_USERS';
const DELETE_USERS = 'admin/DELETE_USERS';

const createUsers = data => {
  return axios.post(`${apiUri}/create`, data);
};

const fatchUsers = () => {
  return axios.post(`${apiUri}/fatch`);
};

const editUsers = data => {
  return axios.post(`${apiUri}/edit`, data);
};

const deleteUsers = data => {
  return axios.post(`${apiUri}/delete`, data);
};

export const createusers = createAction(CREATE_USERS, createUsers);
export const fatchusers = createAction(FATCH_USERS, fatchUsers);
export const editusers = createAction(EDIT_USERS, editUsers);
export const deleteusers = createAction(DELETE_USERS, deleteUsers);

const initialState = {
  users: [],
};

export default handleActions(
  {
    ...pender({
      type: FATCH_USERS,
      onSuccess: (state, action) => {
        return produce(state, draft => {
          // eslint-disable-next-line no-param-reassign
          draft.users = action.payload.data;
        });
      },
    }),
  },
  initialState,
);
