import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';

// MATERIAL-UI
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContentText from '@material-ui/core/DialogContentText';

// REDUX
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';

// ACTION
import * as spendsActions from '../store/modules/spends';
import * as salesActions from '../store/modules/sales';
import * as adminActions from '../store/modules/admin';

class DialogDeleteComponent extends PureComponent {
  static propTypes = {
    open: PropTypes.bool.isRequired,
    onClose: PropTypes.func.isRequired,
    data: PropTypes.objectOf(String).isRequired,
    type: PropTypes.string.isRequired,
    date: PropTypes.objectOf(String).isRequired,
    SalesActions: PropTypes.objectOf(Function).isRequired,
    SpendsActions: PropTypes.objectOf(Function).isRequired,
    AdminActions: PropTypes.objectOf(Function).isRequired,
  };

  deleteSales = data => {
    const { SalesActions, onClose, date } = this.props;
    SalesActions.deletesales(data);
    SalesActions.fatchsales(date);
    onClose();
  };

  deleteSpends = data => {
    const { SpendsActions, onClose, date } = this.props;
    SpendsActions.deletespends(data);
    SpendsActions.fatchspends(date);
    onClose();
  };

  deleteUsers = data => {
    const { AdminActions, onClose } = this.props;
    AdminActions.deleteusers(data);
    AdminActions.fatchusers();
    onClose();
  };

  deleteTitle = type => {
    switch (type) {
      case 'sales':
        return '판매 상품 삭제';
      case 'spends':
        return '추가 지출 삭제';
      case 'admin':
        return '유저 삭제';
      default:
        return null;
    }
  };

  deleteAction = type => {
    const { data } = this.props;
    const { deleteSales, deleteSpends, deleteUsers } = this;
    switch (type) {
      case 'sales':
        return () => deleteSales(data);
      case 'spends':
        return () => deleteSpends(data);
      case 'admin':
        return () => deleteUsers(data);
      default:
        return null;
    }
  };

  render() {
    const { open, onClose, type } = this.props;
    const { deleteTitle, deleteAction } = this;
    return (
      <>
        <Dialog
          open={open}
          onClose={onClose}
          aria-labelledby="form-dialog-title"
        >
          <DialogTitle id="form-dialog-title">{deleteTitle(type)}</DialogTitle>
          <DialogContent>
            <DialogContentText>정말로 삭제 하시겠습니까?</DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button onClick={deleteAction(type)}>확인</Button>
            <Button onClick={onClose}>취소</Button>
          </DialogActions>
        </Dialog>
      </>
    );
  }
}

const mapStateToProps = state => ({
  date: state.date,
});

const mapDispatchToProps = dispatch => ({
  SalesActions: bindActionCreators(salesActions, dispatch),
  SpendsActions: bindActionCreators(spendsActions, dispatch),
  AdminActions: bindActionCreators(adminActions, dispatch),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(DialogDeleteComponent);
